
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {httpUrl} from '../../http_url';
const list = () => [
{title:'全部',id:0},
{title:'待签署',id:1},
{title:'待支付',id:2},
{title:'还款中',id:5},
{title:'已还清',id:4},
{title:'已关闭',id:6}
]

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
phone:'',
showMenus: false,
list2: list(),
demo2: '全部',
index:0,
billList:[],
length:'',
}
},
created(){
this.getData(0);
},
components: {
XHeader,
PopupHeader,
Actionsheet,
Card,
Tab, TabItem,
},
methods:{
getData(id){
console.log(id)
const _this = this;
this.$vux.loading.show({
text: '正在加载中...'
})
let postData = this.$qs.stringify({
status:id
})
this.$axios({
method: 'post',
url:httpUrl.orderList,
data:postData
}).then((res)=>{
console.log(res.data)
this.$vux.loading.hide();
if(res.data.errCode==200){
this.billList = res.data.retData;
this.length = res.data.retData.length;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goBillDetail(item){
this.$router.push({
path:'/bill_detail',
query: {
orderid:item.orderid
}
});
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goHome(){
this.$router.push('/home');
},
goCreditInfoList(){
this.$router.push('/credit_info_list');
}
}
}



